.App {
	height: 100vh;
	text-align: center;
	display: flex;
	align-items: center;
	flex-direction: column;
	justify-content: center;
}
.btn-play:hover {
	cursor: pointer;
	background-color: black;
	color: white;
	font-weight: bold;
}
.btn-play {
	border-width: 0;
	width: 100px;
	padding: 10px;
	margin-top: 10px;
	border-radius: 4px;
	background: transparent;
	border: 1px solid black;
}
.input-text {
	height: 30px;
	text-align: center;
	width: 300px;
	font-size: 20px;
	outline: none;
	border: 1px solid black;
	border-radius: 4px;
}
.input-text:focus {
	border-top: 1px solid black;
	border-right: 1px solid black;
	border-left: 1px solid black;
	border-bottom: 2px solid black;
}
